import React from 'react';
import { graphql, PageProps } from 'gatsby';

import Box from '@mui/joy/Box';
import Typography from '@mui/joy/Typography';
import AppContainer from '../../layouts/AppContainer';
import Tag from '../../components/Tag';
import Image from '../../components/Image';

export default function BlogPostTemplate({ data }: PageProps<Queries.BlogPostPageQuery>) {
  const markdownRemark = data.markdownRemark!;
  const frontmatter = markdownRemark.frontmatter!;
  const html = markdownRemark.html!;

  return (
    <AppContainer title={frontmatter.title!} isArticle={true}>
      {frontmatter?.cover && (
        <Box
          sx={{
            overflow: 'hidden',
            borderRadius: '20px',
            position: 'absolute',
            top: 0,
            zIndex: -1,
          }}
        >
          <Image src={frontmatter.cover}></Image>
        </Box>
      )}
      <Box>
        <Box
          sx={{
            width: '96%',
            maxWidth: '800px',
            marginLeft: 'auto',
            marginRight: 'auto',
          }}
        >
          <Box
            sx={{
              display: 'inline-block',
              width: '100%',
              marginTop: '320px',
              padding: '32px',
              minHeight: '840px',
              background: 'white',
            }}
          >
            <h1
              style={{
                fontSize: '1.875rem',
              }}
            >
              {frontmatter.title}
            </h1>

            <h2>{frontmatter.date}</h2>

            {frontmatter.tags?.length && frontmatter.tags!.map((name: string | null) => <Tag name={name || ''}></Tag>)}

            <Typography level="body2" mt={2} mb={2}>
              <div dangerouslySetInnerHTML={{ __html: html }}></div>
            </Typography>
          </Box>
        </Box>
      </Box>
    </AppContainer>
  );
}

export const pageQuery = graphql`
  query BlogPostPage($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        slug
        title
        cover
        tags
      }
    }
  }
`;
